import "./app.css";
import { LiveEditor, LiveError, LivePreview, LiveProvider } from "react-live";
function App() {
  const code = `
() => {
    const [count, setCount] = React.useState(0);
    return (
        <>
            <p>Count: {count}</p>
            <button onClick={() => setCount(count + 1)}>+1</button>
            <button onClick={() => setCount(count - 1)}>-1</button>
        </>
    )
}
`;
  return (
    <div className="app">
      <h1>This is Embedded React application in Html, a live code Editor</h1>
      <LiveProvider code={code}>
      <LiveEditor />
      <LiveError />
      <LivePreview />
    </LiveProvider>
    </div>
  );
}

export default App;
